import { Component, OnInit, OnChanges, Input, Output, EventEmitter, ChangeDetectorRef } from '@angular/core';
import { Address } from './address';
import { GeoLocation } from './geo.location';
declare var google;

@Component({
  selector: 'app-address',
  templateUrl: './address.component.html',
  styleUrls: ['./address.component.css']
})
export class AddressComponent implements OnInit, OnChanges {
  @Input() address: Address;
  @Output() onChange = new EventEmitter<Address>();

  center: GeoLocation = new GeoLocation(38.049, 23.769);
  model: Address = new Address();
  map: any;
  marker: any;

  constructor(private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.map = new google.maps.Map(document.getElementById('map'), {
      center: this.center,
      zoom: 12
    });

    this.marker = new google.maps.Marker({
      position: this.model.location,
      map: this.map,
      draggable: true
    });

    let cmp = this;
    google.maps.event.addListener(this.marker, 'dragend', () => {
      let geocoder = new google.maps.Geocoder;
      geocoder.geocode({ 'location': this.marker.getPosition() }, (results, status) => {
        if (status === 'OK') {
          if (results[0]) {
            let addr = results[0].address_components;
            this.model.location = this.marker.getPosition();
            this.model.street = addr.find((obj) => { return obj.types.indexOf('route') > -1; }).long_name;
            this.model.streetNumber = addr.find((obj) => { return obj.types.indexOf('street_number') > -1; }).long_name;
            this.model.area = addr.find((obj) => { return obj.types.indexOf('locality') > -1; }).long_name;
            this.model.postalCode = addr.find((obj) => { return obj.types.indexOf('postal_code') > -1; }).long_name;
            this.model.country = addr.find((obj) => { return obj.types.indexOf('country') > -1; }).long_name;
            this.change(this.model);
            this.cdRef.detectChanges();
          } else {
            this.marker.setPosition(this.model.location);
          }
        } else {
          this.marker.setPosition(this.model.location);
        }
      });
    });
  }

  ngOnChanges(changes: any): void {
    let addressChange: Address = changes.address.currentValue;
    if (addressChange) {
      this.model = addressChange;
      if (this.model.location.lat === 0 && this.model.location.lng === 0) {
        this.model.location = this.center;
      }
      this.marker.setPosition(this.model.location);
    }
  }

  updateMarker() {
    let geocoder = new google.maps.Geocoder();
    let addr = this.model.street + ' ' + this.model.streetNumber + ', ' + this.model.area + ' ' + this.model.postalCode;
    geocoder.geocode({ 'address': addr }, (results, status) => {
      if (status === 'OK') {
        let latlng = results[0].geometry.location;
        this.model.location = latlng;
        this.map.setCenter(latlng);
        this.marker.setPosition(latlng);
        this.change(this.model);
      } else {
        swal('Warning', 'Geocode was not successful for the following reason: ' + status, 'warning');
      }
    });
  }

  change(addr: Address) {
    this.onChange.emit(addr);
  }
}



