import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Headers, RequestOptions } from '@angular/http';
import { Building } from './building';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';

@Injectable()
export class BuildingService {
    private apiUrl = 'http://localhost:8080/api/buildings';

    constructor(private http: Http) { }

    getRows(): Observable<Building[]> {
        return this.http.get(this.apiUrl)
            .map(this.extractData)
            .catch(this.handleError);
    }

    getRow(id: number): Observable<Building> {
        return this.http.get(this.apiUrl + '/' + id)
            .map(this.extractData)
            .catch(this.handleError);
    }

    addRow(b: Building): Observable<Building> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.apiUrl, b, options)
            .map(this.extractData)
            .catch(this.handleError);
    }

    updateRow(b: Building): Observable<Building> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.put(this.apiUrl + '/' + b.id, b, options)
            .map(this.extractData)
            .catch(this.handleError);
    }

    deleteRow(id: number): Observable<Building> {
        return this.http.delete(this.apiUrl + '/' + id)
            .map(this.extractData)
            .catch(this.handleError);
    }

    private extractData(res: Response) {
        let body = res.json();
        return body;
    }

    private handleError(error: Response | any) {
        // In a real world app, we might use a remote logging infrastructure
        let errMsg: string;
        // if (error instanceof Response) {
        //     const body = error.json() || '';
        //     const err = body.error || JSON.stringify(body);
        //     errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
        // } else {
            errMsg = error.message ? error.message : error.toString();
        // }
        console.log(errMsg);
        return Observable.throw(errMsg);
    }
}



