import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { MyNavComponentComponent } from './my-nav-component/my-nav-component.component';
import { BuildingsComponent } from './buildings/buildings.component';
import { HomeComponent } from './home/home.component';
import { AddressComponent } from './address/address.component';
import { AppartmentsComponent } from './appartments/appartments.component';
import { BuildingComponent } from './building/building.component';
import { LoadingIndicatorComponent } from './loading-indicator/loading-indicator.component';

@NgModule({
  declarations: [
    AppComponent,
    MyNavComponentComponent,
    BuildingsComponent,
    HomeComponent,
    AddressComponent,
    AppartmentsComponent,
    BuildingComponent,
    LoadingIndicatorComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot([
        { path: '', redirectTo: 'home', pathMatch: 'full' },
        { path: 'home', component: HomeComponent },
        { path: 'buildings', component: BuildingsComponent },
        { path: 'buildings/:id', component: BuildingComponent },
        { path: '**', redirectTo: 'home' }
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



